import React, { useContext } from 'react';
import { Link } from 'react-router-dom';
import UserContext from '../../contexts/UserContext';
import './style.css';

export function Login() {

  const {login, reservaUsuario} = useContext(UserContext);
  
  return (
    <section className='login'>
            {reservaUsuario
            ?
            <h4 className='usuarioSinReserva'>Para confirmar un alquiler por favor primero inicie sesin</h4>
            :
            null}
            <h2>Iniciar sesin</h2>
            <form className='form-login'  onSubmit={login}>        
              <label>Correo Electrnico<input type="email" required name='email'/></label>
              <label>Contrasea<input type="password" required minLength="6" name='pass' /></label>
              <input type="submit" value="Ingresar" className='btn-submit' />
            </form>
          <p>An no tienes cuenta? <Link to='../registro'>Registrate</Link></p>
          
    </section>
  )
}