import React, { useContext } from 'react';
import { Link } from 'react-router-dom';
import UserContext from '../../contexts/UserContext';
import './style.css';

export function Registro() {

  const {saveUser} = useContext(UserContext);
  
  return (
    <section className="registro">
      <h2>Crear cuenta</h2>
      <form className='form-registro' onSubmit={saveUser}>  
        <div className="doble-campo">      
        <label>Nombre<input type="text" required name='nombre'/></label>
        <label>Apellido<input type="text" required name='apellido'/></label>
        </div>
        <label>Correo Electrnico<input required type="email" name='email'/></label>
        <label>Contrasea<input type="password" required name='pass1'/></label>
        <label>Confirmar contrasea<input type="password"required name='pass2'/></label>
        <input type="submit" value="Crear cuenta" className='btn-submit'/>
      </form>
      <p>Ya tienes una cuenta? <Link to='../login'>Iniciar sesin</Link></p>
    </section>
  )
}